<template>
  <div>
      <Load :hideLoad="hideLoad"></Load>
    <div :class="{hide:!hideLoad}">
        <h3 class="title">详情描述</h3>
        <el-input type="textarea"
                  rel="inputWidth"
                  v-model="description"
                  @change="onChangeDesc"
                  autosize>
        </el-input>
        <h3 class="title">文件列表</h3>
        <div class="tab-tit clear">
          <TabTit class="fl"
                  :item="item"
                  :index="index"
                  :selected="selected"
                  v-for="(item,index) in files"
                  :key="index"
                  @onchangeTab="onChangeTabHandle(index)">
          </TabTit>
          <el-button size="mini" @click="addFile" v-show="!savable">添加</el-button>
        </div>
        <File
          :item="item"
          :index="index"
          :key="index"
          v-for="(item,index) in files">
        </File>
        <div class="btn" v-show="savable">
          <el-button type="primary"
                     size="medium"
                     :disabled="!savable"
                     @click="onUpdateItem">
            保存
          </el-button>
        </div>
      </div>
  </div>
</template>

<script>
  import http from "axios"
  import bus from './../../bus.js'
  import TabTit from '@/components/List/TabTit'
  import Load from '@/components/List/Load'
  import File from '@/components/List/File'

  export default {
    name: "Gist",
    components: {Load, TabTit, File},
    data() {
      return {
        id: this.$route.query.id,
        hideTab: true,
        hideLoad: false,
        showAdd: true,
        files: [],
        activeName: "",
        selected: 0,
        newFileIndex: 0,
        description: "",
        newContent: "",
        fileCRUD: {}
      }
    },
    beforeRouteUpdate(to, from, next) {
      if (Object.keys(this.fileCRUD).length !== 0) {
        // next(false);
        this.$confirm('检测到未保存的内容，是否在离开页面前保存修改？', '确认信息', {
          distinguishCancelAndClose: true,
          confirmButtonText: '保存',
          cancelButtonText: '放弃修改'
        })
          .then(() => {
            this.$message({
              type: 'info',
              message: '保存修改'
            });
            this.fileCRUD = {};
            this.getFiles(to.query.id);
            this.id = to.query.id;
            next();
          })
          .catch(action => {
            this.$message({
              type: 'info',
              message: action === 'cancel'
                ? '放弃保存并离开页面'
                : '停留在当前页面'
            })
            this.fileCRUD = {};
            this.hideLoad = false;
            this.getFiles(to.query.id);
            this.id = to.query.id;
            next();
          })
      } else {
        this.hideLoad = false;
        this.fileCRUD = {};
        this.getFiles(to.query.id);
        this.id = to.query.id;
        next();
      }
    },
    computed: {
      savable() {
        return this.fileCRUD.deletable
          || this.fileCRUD.editable
          || this.fileCRUD.addable
      }
    },
    mounted() {
      this.getFiles(this.id);
      bus.$on("onEditTitHandle", () => {
      });
      bus.$on("changeSave", () => {
      });
      bus.$on("onNameChangedHandle", (obj) => {
        this.fileCRUD = obj;
      });
      bus.$on("onContentChanged", () => {
        this.$set(this.fileCRUD,"editable",true);
      })
      bus.$on("onDeleteFiles",()=>{
        this.$set(this.fileCRUD,"deletable",true);
      });
    },
    methods: {
      getFiles(id) {
        http.get("https://api.github.com/gists/" + id, {
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        }).then((r) => {
          this.hideLoad = true;
          this.description = r.data.description;
          this.files = Object.values(r.data.files);
          this.files.forEach(function (val) {
            val["oldname"] = val.filename;
          });
          this.activeName = this.files[0].filename;
        })
          .catch();
      },
      onChangeDesc() {
        this.$set(this.fileCRUD, "editable", true);
      },
      onChangeTabHandle(index) {
        this.selected = index;
        bus.$emit("changeContent", index);
      },
      addFile() {
        var filename = "file_" + this.newFileIndex++;
        this.files.push({
          filename: filename,
          content: "",
        });
      },
      onUpdateItem() {
        var data = {};
        data.description = this.description;
        if (this.description && this.description.length === 0) {
          alert("详情描述不能为空");
          return;
        }
        data.files = {};
        this.files.forEach((item) => {
          if (item.oldname !== undefined) {
//                更新文件
            if (item.editable) {
              data.files[item.oldname] = {
                filename: item.filename,
                content: item.content
              }
            }
//                删除文件
            if (item.deletable) {
              data.files[item.oldname] = null;
            }
          } else {
            //                    添加文件
            data.files[item.filename] = {
              content: item.content
            }
          }
        })
        console.log(data);
        http.patch("https://api.github.com/gists/" + this.id, data, {
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        }).then((r) => {
          console.log(r);
          this.open();
          bus.$emit("updateList", {
            id: this.id,
            files: r.data.files,
            description: this.description
          });
          bus.$emit("changeEditContent");
          this.fileCRUD = {};
        })
          .catch();
      },
      open() {
        this.$message({
          message: '恭喜你，保存成功',
          type: 'success'
        });
      }
    }
  }
</script>

<style scoped>
  .tab-tit {
    border-bottom: 2px solid #e4e7ed;
    margin-bottom: 20px;
  }

  .title {
    background-color: #f5f5f5;
    padding: 0 12px;
    height: 30px;
    line-height: 30px;
  }

  .addFile {
    color: #409EFF;
    position: relative;
    top: 15px;
    right: 10px;
  }

  .addFile i {
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
  }

  .btn {
    margin-top: 20px;
  }
</style>



