<template>
  <div :class="{hide: item.deletable,active: selected === index}" class="tabTit">
    <input @change="onNameChanged"
           ref="inputWidth"
           @click="changeTab(index)"
           :readonly="readonly"
           :class="{inputHover:!readonly}"
           v-model="item.filename"
           type="text"/>
    <i class="el-icon-delete" @click="deleteFiles"></i>
  </div>
</template>
<script>
  import bus from "./../../bus";

  export default {
    name: "TabTit",
    props: ["item", "index", "selected"],
    data(){
      return {
        readonly:true,
        isHnputHover:false,
      }
    },
    computed:{
      deletable(){
        return this.item.deleted && this.item.filename === ""
      },
      editable(){
        return this.item.oldname !== this.item.filename
      },
      addable(){
        return this.item.oldname === ""
      }
    },
    mounted(){
      this.$refs.inputWidth.style.width="60px";
    },
    methods: {
      deleteFiles(){
        this.$set(this.item, "deletable", true);
        this.$set(this.item,"filename","");
        bus.$emit("onDeleteFiles");
      },
      changeTab(index){
        this.readonly = true;
        this.readonly = false;
        this.$emit("onchangeTab",index);
      },
      onNameChanged(){
        this.$set(this.item, "editable", true);
        bus.$emit("onNameChangedHandle",{
          deletable:this.deletable,
          editable:this.editable,
        });
      }
    }
  }
</script>
<style scoped>
  span{
    /*display: inline-block;*/
  }
  .tabTit{
    position: relative;
  }
  .active input
  {
    border: 1px solid #409eff;
  }
  input {
    border:1px solid #eee;
    border-radius:4px;
    padding: 5px 10px;
    outline: none;
    margin-right: 20px;
    margin-bottom: 20px;
    position: relative;
  }
  .el-icon-delete {
    position: absolute;
    display: none;
    top: 8px;
    right: 30px;
    font-size: 12px;
  }
  .active .el-icon-delete {
    cursor: pointer;
    display: block;
  }
</style>



