<template>
  <el-form>
    <div class="form-item">
      <el-input v-model="item.filename"
                placeholder="请输入文件名称"
                @change="$emit('inputChange',index)"></el-input>
      {{note}}
    </div>
    <div class="form-item">
        <el-input type="textarea"
                  v-model="item.content"
                  autosize
                  placeholder="请输入文件内容"
                  @change="$emit('contentChange',index)">
        </el-input>
    </div>
  </el-form>
</template>
<script>
  import http from "axios"
  import prism from 'prismjs'
  import 'prismjs/themes/prism.css'

  export default {
    name: "TabContent",
    props: ["files", "item", "index", "id", "addHandle"],
    data() {
      return {
        note: "",
      }
    },
    watch: {
      'item.filename'(newVal) {
        //edit
        var arr = this.files.filter(val => val.deleted !== undefined);
        //add
        if (this.addHandle) {
          arr = this.files;
        }
        if ((arr.filter(val => val.filename === newVal)).length > 1) {
          this.note = "文件名重复，请重新填写";
        } else {
          this.note = "";
        }
      },
      'item.content'(newVal){
//        this.files[0].content = prism.highlightAll(this.files[0].content, prism.languages.javascript)
      }
    },
    methods:{
      prismContent(index){

      }
    }
  }
</script>
<style scoped>
  .form-item{
    padding: 0;
  }
  pre{
    position: relative;
    top:0;left:340px;
  }
</style>



